import React, {Component} from "react";
import Footer from "./Footer.js";
import Navbar from "./Navbar.js";
import Swal from "sweetalert2";
import MetaTags from "react-meta-tags";
import AutoPartForm from "./AutoPartForm";

export default class Contact extends Component {
  submituserInquiryForm() {
    var carbattery = document.getElementById("carbattery").value;
    var area = document.getElementById("area").value;
    var contactno = document.getElementById("contactno").value;
    var make = document.getElementById("make").value;
    var submitted = true;

    if (carbattery === "") {
      submitted = false;
      document.getElementById("carbatteryerror").innerHTML = "** Please fill the Battery Model";
      // return false;
    } else if (area === "") {
      submitted = false;
      document.getElementById("areaerror").innerHTML = "** Please fill the Area";
      // return false;
    } else if (contactno === "") {
      submitted = false;
      document.getElementById("contactnoerror").innerHTML = "** Please fill the Contact";
      // return false;
    } else if (isNaN(contactno)) {
      submitted = false;
      document.getElementById("contactnoerror").innerHTML = "** Check Your contact no";
      // return false;
    } else if (make === "") {
      submitted = false;
      document.getElementById("makeerror").innerHTML = "** Select your make";
      // return false;
    } else {
      if (submitted) {
        Swal.fire("Form submitted. We will contact you shortly ;)").then(() => document.getElementById("myForm").onreset(window.location.reload()));
        return submitted;
      }
    }
  }
  render() {
    return (
      <div>
        <MetaTags>
          <meta charSet="utf-8" />
          <title>AutoB</title>
          <meta
            name="title"
            content="Car Battery Dubai & Sharjah | AutoB"
          />
          <meta
            name="description"
            content="Car Battery Replacement | Replace your car battery anytime at Dubai and Sharjah. We offer the best battery replacement service. Delivery & installation service at best price. 24/7 support. We offer battery replacement for battery such as Solite, Amaron, Acdelco, Varta."
          />
        </MetaTags>
        <Navbar/>
        <div className="container pt-5 text-center">
          <div className="row justify-content-center">
            <h1 className="heading">Auto Spare parts for any vehicle brands of any country.</h1>
          </div>
        </div>

        <div class="container py-5 form-bg">
          <div class="row justify-content-center">
            <div className="col-md-5">
              <iframe src="https://www.google.com/maps/embed?pb=!1m10!1m8!1m3!1d462423.69677338307!2d55.0367571!3d25.1125178!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2sin!4v1625891911862!5m2!1sen!2sin" width="450" height="450" style={{border:"0"}} title="car battery dubai" allowfullscreen="" loading="lazy"></iframe>
            </div>
            <div class="col-md-7 col-xs-12 col-sm-10  ">
            <h1 className="paragraph">Order your Auto Parts now!</h1>
            <hr/>
              <AutoPartForm/>
            </div>
          </div>

        </div>

        <div className="text-center">
          <Footer/>
        </div>

      </div>
    );
  }
}
